@import url("https://fonts.googleapis.com/css?family=Montserrat:400,900;subset=latin-ext");
body {
  font-family: "Montserrat", Arial, sans-serif;
  font-size: 14px;
  margin: 0;
  padding: 0;
  position: relative;
}

h1, .h1 {
  font-weight: 700;
  font-size: 2rem;
  text-transform: uppercase;
}

h2, .h2 {
  font-weight: 700;
  font-size: 1.5rem;
}

h3, .h3 {
  font-weight: 400;
  font-size: 1.5rem;
}

h4, .h4 {
  font-weight: 400;
  font-size: 1.2rem;
}

p {
  margin: 0;
}

.wrapper {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

.italics {
  font-style: italic;
}

.underline {
  text-decoration: underline;
}

.flex--wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  align-content: center;
}
.flex--item {
  flex-basis: 200px;
  align-self: flex-end;
}

:link,
:active,
:visited {
  color: inherit;
}

header {
  position: absolute;
  width: 100%;
  height: 20vh;
  top: 0;
  left: 0;
  display: none;
}

section {
  min-height: 60vh;
  padding: 10vh 10vh;
  position: relative;
}

@keyframes shake {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }
  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }
  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }
  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}
@keyframes move_wave {
  0% {
    transform: translateX(0) translateZ(0) scaleY(1);
  }
  50% {
    transform: translateX(-25%) translateZ(0) scaleY(0.55);
  }
  100% {
    transform: translateX(-50%) translateZ(0) scaleY(1);
  }
}
@keyframes move_wave_rotated {
  0% {
    transform: translateX(0) translateZ(0) scaleY(1);
  }
  50% {
    transform: translateX(25%) translateZ(0) scaleY(0.55);
  }
  100% {
    transform: translateX(50%) translateZ(0) scaleY(1);
  }
}
.waveWrapper {
  overflow: hidden;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
}
.waveWrapper.rotated {
  transform: rotate(180deg);
}

.waveWrapperInner {
  position: absolute;
  width: 100%;
  overflow: hidden;
  height: 100%;
  bottom: -1px;
}

.bgTop {
  z-index: 15;
}

.bgMiddle {
  z-index: 10;
}

.bgBottom {
  z-index: 5;
}

.wave {
  position: absolute;
  left: 0;
  width: 200%;
  height: 100%;
  background-repeat: repeat no-repeat;
  background-position: 0 bottom;
  transform-origin: center bottom;
}

.rotated .wave {
  right: 0;
  left: unset;
}

.waveTop {
  background-size: 50% 100px;
}

.waveMiddle {
  background-size: 50% 120px;
}

.waveBottom {
  background-size: 50% 100px;
}

.waveAnimation .waveBottom {
  animation: move_wave 5s linear infinite;
  -webkit-animation: move_wave 5s linear infinite;
  -webkit-animation-delay: 5s;
  animation-delay: 5s;
}
.waveAnimation .waveMiddle {
  animation: move_wave 8s linear infinite;
}
.waveAnimation .waveTop {
  animation: move_wave 12s linear infinite;
}
.waveAnimation.rotated .waveBottom {
  animation: move_wave_rotated 5s;
  -webkit-animation: move_wave_rotated 5s;
  -webkit-animation-delay: 5s;
  animation-delay: 5s;
}
.waveAnimation.rotated .waveMiddle {
  animation: move_wave_rotated 10s linear infinite;
  -webkit-animation: move_wave_rotated 10s linear infinite;
}
.waveAnimation.rotated .waveTop {
  animation: move_wave_rotated 20s linear infinite;
  -webkit-animation: move_wave_rotated 20s linear infinite;
}

#section--intro {
  padding: 0 10vh 20vh;
  position: relative;
}
#section--intro .section__contentWrapper {
  box-sizing: border-box;
  padding: 20vh 10vw 10vh;
  text-align: center;
  position: relative;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 10;
}
#section--intro .intro__logo {
  width: 35%;
  display: block;
  margin: 0 auto 30px;
  z-index: 10;
}
#section--intro .intro__subtitle {
  font-size: 2rem;
  margin-bottom: 5vh;
  z-index: 10;
}
#section--intro .intro__button {
  background: none;
  border: none;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  margin: 0 auto;
  font-size: 1rem;
  padding: 0.5rem 1.5rem;
  z-index: 10;
  font-size: 1.2rem;
  opacity: 0.5;
}
#section--intro .intro__video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
  opacity: 0.3;
}
#section--fields {
  padding: 25vh 10vh 15vh;
}
#section--lists {
  position: relative;
}
#section--lists:before {
  content: "";
  position: absolute;
  top: -2px;
  left: 5vw;
  width: 90vw;
  height: 1px;
  background-color: rgb(113.5, 113.5, 113.5);
}

.section__contentWrapper {
  width: 100%;
  min-height: 600px;
}
.section__title {
  font-weight: 700;
  font-size: 3rem;
  position: relative;
}
.section__title:after {
  content: "";
  display: block;
  position: absolute;
  width: 10%;
  height: 10px;
  bottom: -5px;
  left: 0;
  background-blend-mode: darken;
}
.section__title:nth-of-type(2n+1):after {
  background-color: #28abef;
}
.section__title:nth-of-type(2n):after {
  background-color: #57c177;
}

.field__overview-container {
  padding-bottom: 3em;
  position: relative;
}
.field__overview-name {
  font-weight: 700;
}
.field__overview-expander {
  border: 3px solid #252525;
  font-weight: 700;
  font-size: 1rem;
  background: none;
  padding: 0.5ex 1ex;
  position: absolute;
  bottom: 0;
  right: 0;
}

.fieldDetails__wrapper {
  min-height: 100%;
  overflow: hidden;
}
.fieldDetails__list {
  justify-content: space-evenly;
}
.fieldDetails__list-element {
  list-style: none;
  padding: 15px 0;
  margin: 30px 20px;
  flex-basis: 22%;
  position: relative;
}
.fieldDetails__repo-thumbnail {
  width: 150px;
  height: auto;
  margin: 0 calc(50% - 75px);
}
.fieldDetails__repo-thumbnail:hover {
  animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  perspective: 1000px;
}
.fieldDetails__repo-name {
  font-weight: 700;
}
.fieldDetails__repo-desc {
  text-align: justify;
  min-height: 8em;
}
.fieldDetails__repo-link {
  font-weight: 700;
  border-bottom: 3px solid #252525;
  margin-top: 10px;
  display: inline-block;
  text-decoration: none;
  position: absolute;
  right: 0;
  bottom: 0;
}
.fieldDetails__repo-link:hover {
  border-bottom-color: #57c177;
}

footer {
  padding: 5px 0;
  height: 1.5rem;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: transparent;
  color: #aaa;
}
footer p {
  text-align: center;
  opacity: 0.6;
}

@media only screen and (max-width: 1024px) {
  #section--intro .intro__logo {
    width: 80%;
  }
  #section--intro .intro__video {
    height: 100%;
    width: auto;
    margin-left: -50%;
  }
  .fieldDetails__list {
    padding: 0;
  }
  .fieldDetails__list-element {
    flex-basis: 45%;
  }
}
@media only screen and (max-width: 640px) {
  section {
    padding: 2vh 2vh 4vh 2vh;
  }
  #section--intro .section__contentWrapper {
    padding-top: 40vh;
    height: calc(100vh - 2vh - 2rem);
  }
  #section--intro .intro__subtitle {
    font-size: 0.8rem;
  }
  #section--intro .intro__button {
    font-size: 1rem;
  }
  .section__title {
    width: 80%;
    margin: 0 auto;
  }
  .flex--item {
    flex-basis: 70%;
  }
  .field__overview-container {
    margin-bottom: 15px;
    box-sizing: border-box;
  }
  .field__overview-description {
    text-align: justify;
  }
  .fieldDetails__list {
    padding: 0;
  }
  .fieldDetails__list-element {
    flex-basis: 70%;
  }
  .fieldDetails__repo-desc {
    min-height: unset;
    padding-bottom: 0.5em;
  }
  .fieldDetails__repo-link {
    bottom: -0.5em;
  }
  .fieldDetails__repo-thumbnail {
    margin-top: 20px;
    max-width: 60%;
  }
}